.trans-block{
    position: absolute;
    background:white;
    opacity: .3;
    top: 0;
    bottom:0;
    left:0;
    right:0;
    z-index: 100;

}

.trans-block + .progress{
    position: absolute;
    top:40%;
    left:45%;
    z-index:101;
}


